// @flow
import React, { useState, Fragment } from 'react';
import type { Element } from 'react';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import _get from 'lodash/get';
import {
  APP_BASE_URL,
  OFFER_STEPS,
  STEPS_AWAITING_SELLER_ACTION,
} from 'shared/Consts';
import type { Offer } from 'types/Offer';

type Props = {
  offer: Offer,
  handleApproval: Function,
  isSubmitting: boolean,
  handleDecline: Function,
};

export const ActionButtons = ({
  offer,
  handleApproval,
  isSubmitting,
  handleDecline,
}: Props): Element<typeof Fragment> => {
  const [anchorEl, setAnchorEl] = useState(null);
  const offerId = _get(offer, ['OfferId']);
  const stepId = _get(offer, ['StepId']);
  const formId = _get(offer, ['CounterOffer', 'Forms', 0, 'FormId']);
  const counterOfferId = _get(
    offer,
    ['CounterOffer', 'CounterOfferId'],
    offerId,
  );
  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const isInvalidSubmission =
    isSubmitting || !STEPS_AWAITING_SELLER_ACTION.includes(stepId);

  const onClickApprovalHandler = () => {
    if (isInvalidSubmission) return;

    handleApproval(offerId);
    handleClose();
  };

  const onClickDeclineHandler = () => {
    if (isInvalidSubmission) return;

    handleDecline(offerId);
    handleClose();
  };

  return (
    <>
      <button
        type="button"
        className="minimal-btn animated_fastest"
        onClick={handleClick}
        disabled={isInvalidSubmission}
      >
        <i className="fa fa-ellipsis-h"></i>
      </button>
      <Menu
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <MenuItem component="button" onClick={onClickApprovalHandler}>
          Accept
        </MenuItem>
        <MenuItem component="button" onClick={onClickDeclineHandler}>
          Decline
        </MenuItem>
        {stepId !== OFFER_STEPS.buyerAcceptCounterOffer && (
          <MenuItem
            component="a"
            href={`${APP_BASE_URL}/offer?offerId=${counterOfferId}&step=${OFFER_STEPS.counteredBySeller}&formId=${formId}`}
          >
            Counter Offer
          </MenuItem>
        )}
      </Menu>
    </>
  );
};
