// @flow
import React, { memo } from 'react';
import type { Element } from 'react';
import Table from 'components/Table';
import Loader from 'components/Loader';
import { ActionButtons } from './ActionButtons';
import { hideIfInvalidProps } from 'utils/RenderComponent';
import { mappingCounterData } from 'helpers/Offer';
import _get from 'lodash/get';

type Props = {
  offers: Array<Object>,
  isLoading: boolean,
  handleApproval: Function,
  isSubmitting: boolean,
  handleDecline: Function,
};

const checkProps = ({ offers }) => offers && offers.length;

const SellerOfferList = (props: Props): Element<'div'> => {
  const {
    offers,
    isLoading,
    handleApproval,
    isSubmitting,
    handleDecline,
  } = props;

  const mappingOffers = offers.map(mappingCounterData);

  const renderItem = ({ value }) => <span>{value || '-'}</span>;

  const renderActions = ({ row }) => (
    <ActionButtons
      offer={_get(row, ['original'])}
      handleApproval={handleApproval}
      isSubmitting={isSubmitting}
      handleDecline={handleDecline}
    />
  );

  const columns = [
    {
      Header: 'ID',
      accessor: 'OfferNo',
      Cell: renderItem,
    },
    {
      Header: 'Offer Price',
      accessor: 'MappingCounter.OfferPrice',
      Cell: renderItem,
    },
    {
      Header: 'Funding',
      accessor: 'Funding.PurchaseType',
      Cell: renderItem,
    },
    {
      Header: 'Loan Type',
      accessor: 'Funding.LoanType',
      Cell: renderItem,
    },
    {
      Header: 'Down Payment',
      accessor: 'Funding.DownPayment',
      Cell: renderItem,
    },
    {
      Header: 'Due Diligence Contingency',
      accessor: 'MappingCounter.InspectionContingency',
      Cell: renderItem,
    },
    {
      Header: 'Sale of Home Contingency',
      accessor: 'MappingCounter.SaleOfHomeContingency',
      Cell: renderItem,
    },
    {
      Header: 'Closing Period',
      accessor: 'MappingCounter.ClosingPeriod',
      Cell: renderItem,
    },
    {
      Header: 'Furniture',
      accessor: 'MappingCounter.Furniture',
      Cell: renderItem,
    },
    {
      Header: 'Current Status',
      accessor: 'Step.messages.seller',
      Cell: renderItem,
    },
    {
      Header: 'Actions',
      Cell: renderActions,
    },
  ];

  return (
    <div className="offer_block">
      <div className="offer_container">
        {isLoading && <Loader />}
        <Table columns={columns} data={mappingOffers} />
      </div>
    </div>
  );
};

export default (hideIfInvalidProps(checkProps)(
  memo(SellerOfferList),
): typeof SellerOfferList);
